.about_us {
  width: 100%;
  height: 100vh;
  position: relative;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about-text {
  position: absolute;
  width: 30%;
  top: 50%;
  left: 18%;
  transform: translate(-50%, -50%);
}
.about-text h1 {
  width: 60%;
  font-size: 3rem;
  font-weight: 800;
  color: black;
  text-transform: uppercase;
  border-right: 5px solid green;
  border-left: 5px solid green;
}
.about-text h2 {
  width: 85%;
  color: green;
  padding: 2rem 0;
  font-size: 1.5rem;
  font-weight: 800;
  text-align: left;
}
.about-text p {
  width: 70%;
  padding: 2rem 0;
  font-size: 1.2rem;
  text-align: left;
}
.show {
  cursor: pointer;
  text-decoration: none;
  position: absolute;
  color: green;
  padding: 2rem 0;
  font-size: 1.2rem;
  left: 0;
}
.show:hover {
  text-decoration: underline;
}
@media screen and (max-width: 1050px) {
  .about_us {
    background-color: #dfdad5;
    height: 50vh;
  }
  .about_us img {
    display: none;
  }
  .about-text {
    position: absolute;
    width: 80%;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
  }
}
@media screen and (max-width: 555px) {
  .about_us {
    background-color: #dfdad5;
    height: 110vh;
  }
  .about-text {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 60%;
    transform: translate(-50%, -50%);
  }

  .about_us img {
    display: none;
  }
  .about-text h1 {
    width: 70%;
  }
  .show {
    padding: 1rem 0;
  }
}
